"use client";

import { useEffect, useState } from "react";
import api from "@/lib/axios";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow
} from "@/components/ui/table";
import { ArrowDownLeft, ArrowUpRight, Search } from "lucide-react";
import { Input } from "@/components/ui/input";

interface Transaction {
    id: number;
    reference: string;
    amount: number;
    type: 'credit' | 'debit';
    status: string;
    description: string;
    created_at: string;
    user?: {
        email: string;
        name: string;
    }
}

export default function AdminTransactionsPage() {
    const [transactions, setTransactions] = useState<Transaction[]>([]);
    const [loading, setLoading] = useState(true);
    const [searchTerm, setSearchTerm] = useState("");

    useEffect(() => {
        api.get('/admin/transactions')
            .then(res => setTransactions(res.data.data))
            .catch(console.error)
            .finally(() => setLoading(false));
    }, []);

    const filteredTransactions = transactions.filter(t =>
        t.reference?.toLowerCase().includes(searchTerm.toLowerCase()) ||
        t.description?.toLowerCase().includes(searchTerm.toLowerCase()) ||
        t.user?.name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
        t.user?.email?.toLowerCase().includes(searchTerm.toLowerCase())
    );

    return (
        <div className="space-y-6">
            <div className="flex flex-col md:flex-row md:items-center justify-between gap-4">
                <h2 className="text-2xl font-bold tracking-tight text-slate-800 dark:text-slate-100">Financial Management</h2>
                <div className="relative w-full md:w-72">
                    <Search className="absolute left-3 top-2.5 h-4 w-4 text-slate-400" />
                    <Input
                        placeholder="Search transactions..."
                        className="pl-9 bg-white dark:bg-slate-900"
                        value={searchTerm}
                        onChange={(e) => setSearchTerm(e.target.value)}
                    />
                </div>
            </div>

            <div className="grid gap-4 md:grid-cols-3">
                {/* Financial Stats Placeholders (could be real if API provided stats) */}
                <Card className="border-none shadow-sm bg-indigo-50 dark:bg-indigo-900/10">
                    <CardHeader className="py-4">
                        <CardTitle className="text-sm font-medium text-indigo-600 dark:text-indigo-400">Total Inflow (Today)</CardTitle>
                    </CardHeader>
                    <CardContent>
                        <div className="text-2xl font-bold text-slate-800 dark:text-slate-100">GHS ---</div>
                    </CardContent>
                </Card>
                <Card className="border-none shadow-sm bg-green-50 dark:bg-green-900/10">
                    <CardHeader className="py-4">
                        <CardTitle className="text-sm font-medium text-green-600 dark:text-green-400">Wallet Deposits</CardTitle>
                    </CardHeader>
                    <CardContent>
                        <div className="text-2xl font-bold text-slate-800 dark:text-slate-100">---</div>
                    </CardContent>
                </Card>
                <Card className="border-none shadow-sm bg-orange-50 dark:bg-orange-900/10">
                    <CardHeader className="py-4">
                        <CardTitle className="text-sm font-medium text-orange-600 dark:text-orange-400">Pending Approvals</CardTitle>
                    </CardHeader>
                    <CardContent>
                        <div className="text-2xl font-bold text-slate-800 dark:text-slate-100">0</div>
                    </CardContent>
                </Card>
            </div>

            <Card className="border-none shadow-[0_2px_10px_0_rgba(67,89,113,0.12)]">
                <CardHeader>
                    <CardTitle>Transaction History</CardTitle>
                </CardHeader>
                <CardContent>
                    <Table>
                        <TableHeader>
                            <TableRow>
                                <TableHead>Reference</TableHead>
                                <TableHead>User</TableHead>
                                <TableHead>Type</TableHead>
                                <TableHead>Amount</TableHead>
                                <TableHead>Status</TableHead>
                                <TableHead>Date</TableHead>
                            </TableRow>
                        </TableHeader>
                        <TableBody>
                            {loading ? (
                                <TableRow>
                                    <TableCell colSpan={6} className="text-center h-24 text-muted-foreground">Loading...</TableCell>
                                </TableRow>
                            ) : filteredTransactions.length === 0 ? (
                                <TableRow>
                                    <TableCell colSpan={6} className="text-center h-24 text-muted-foreground">No transactions found.</TableCell>
                                </TableRow>
                            ) : (
                                filteredTransactions.map((trx) => (
                                    <TableRow key={trx.id}>
                                        <TableCell className="font-mono text-xs">{trx.reference}</TableCell>
                                        <TableCell>
                                            <div className="flex flex-col">
                                                <span className="font-medium">{trx.user?.name || 'Unknown'}</span>
                                                <span className="text-xs text-muted-foreground">{trx.user?.email}</span>
                                            </div>
                                        </TableCell>
                                        <TableCell>
                                            <div className={`flex items-center gap-1 font-medium ${trx.type === 'credit' ? 'text-green-600' : 'text-slate-600'}`}>
                                                {trx.type === 'credit' ? <ArrowUpRight className="w-3 h-3" /> : <ArrowDownLeft className="w-3 h-3" />}
                                                <span className="capitalize">{trx.type}</span>
                                            </div>
                                        </TableCell>
                                        <TableCell className="font-bold">
                                            {trx.type === 'credit' ? '+' : '-'}GHS {trx.amount}
                                        </TableCell>
                                        <TableCell>
                                            <Badge variant="secondary" className={
                                                trx.status === 'success' ? 'bg-green-100 text-green-700' :
                                                    trx.status === 'failed' ? 'bg-red-100 text-red-700' : 'bg-orange-100 text-orange-700'
                                            }>
                                                {trx.status}
                                            </Badge>
                                        </TableCell>
                                        <TableCell className="text-muted-foreground text-xs">
                                            {new Date(trx.created_at).toLocaleString()}
                                        </TableCell>
                                    </TableRow>
                                ))
                            )}
                        </TableBody>
                    </Table>
                </CardContent>
            </Card>
        </div>
    );
}
